import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import ThemeContext from '../../context/ThemeContext';
import styles from './Home.module.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import ResumeButton from './ResumeButton';

const Home = () => {
    const { t } = useTranslation();
    const { theme } = useContext(ThemeContext);
    const [socialBubbles, setSocialBubbles] = useState([]);

    const socialMedia = [
        { name: 'LinkedIn', icon: 'linkedin', url: 'https://www.linkedin.com/in/kpa1sai' },
        { name: 'GitHub', icon: 'github', url: 'https://github.com/kpa1sai' },
        { name: 'Twitter', icon: 'twitter', url: 'https://twitter.com/kpa1sai' },
        { name: 'Instagram', icon: 'instagram', url: 'https://www.instagram.com/kpa1sai' },
        { name: 'Facebook', icon: 'facebook-f', url: 'https://www.facebook.com/kpa1sai' },
    ];

    useEffect(() => {
        const bubbles = socialMedia.map((social, index) => ({
            ...social,
            position: { x: Math.random() * 80 + 10, y: Math.random() * 80 + 10 },
            velocity: { x: (Math.random() - 0.5) * 0.5, y: (Math.random() - 0.5) * 0.5 },
        }));
        setSocialBubbles(bubbles);

        const moveBubbles = setInterval(() => {
            setSocialBubbles(prevBubbles => prevBubbles.map(bubble => {
                let newX = bubble.position.x + bubble.velocity.x;
                let newY = bubble.position.y + bubble.velocity.y;

                return {
                    ...bubble,
                    position: { x: newX, y: newY },
                };
            }));
        }, 50);

        return () => clearInterval(moveBubbles);
    }, []);

    return (
        <div className={`${styles.home} ${styles[theme]}`}>
            <div className={styles.content}>
                <h1>{t('home.welcome')}</h1>
                <div className={styles.stats}>
                    <div className={styles.statItem}>
                        <h2>{t('home.projects')}</h2>
                        <p className={styles.statNumber}>25</p>
                    </div>
                    <div className={styles.statItem}>
                        <h2>{t('home.experiences')}</h2>
                        <p className={styles.statNumber}>10</p>
                    </div>
                </div>
                <ResumeButton />
            </div>
            <div className={styles.socialBubbles}>
                {socialBubbles.map((bubble, index) => (
                    <a
                        key={index}
                        href={bubble.url}
                        target="_blank"
                        rel="noopener noreferrer"
                        className={styles.socialBubble}
                        style={{
                            left: `${bubble.position.x}%`,
                            top: `${bubble.position.y}%`,
                        }}
                    >
                        <i className={`fab fa-${bubble.icon}`}></i>
                    </a>
                ))}
            </div>
        </div>
    );
};

export default Home;
